/* footer */
#footer {
  background-color: $base-color;
}
#footer .footermenust {
  display: none;
  flex-wrap: wrap;
  align-items: flex-start;
  text-align: left;
  width: 920px;
  margin: 0 auto;
  padding: 0;
}
#footer .footermenust li {
  display: flex;
  flex-flow: column;
  border: 0;
  position: relative;
  padding: 0;
}
#footer .footermenust > li {
  padding-left: 20px;
  width: 230px;
  margin-bottom: 40px;
}
#footer .footermenust > li::before {
  content: '';
  display: block;
  width: 12px;
  height: 12px;
  background-color: $primary-color;
  border-radius: 50%;
  transform: rotate(90deg);
  position: absolute;
  top: 3px;
  left: 0;
}
#footer .footermenust > li::after {
  content: '';
  display: block;
  border: 4px solid transparent;
  border-bottom: 4px solid #fff;
  transform: translateX(-50%) translateY(-85%) rotate(90deg);
  position: absolute;
  top: 12px;
  left: 8px;
}
#footer .footermenust li a {
  font-size: 14px;
}
#footer .footermenust li a:hover {
  color: $secondary-color;
}
#footer .footermenust > li > a {
  color: #222;
}
#footer .sub-menu {
  margin-top: 15px;
}
#footer .sub-menu li {
  margin-bottom: 5px;
}
#footer .sub-menu li::before {
  display: block;
  content: '';
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 2.5px 0 2.5px 5px;
  position: absolute;
  border-color: transparent transparent transparent $primary-color;
  top: calc(50% - 3px);
  left: -15px;
}
#footer .sub-menu li a {
  color: #666;
}
