/* header */
#headbox {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 0 !important;
}
.telno {
  display: none;
}
.header-r__top,
.menu-utility__bottom {
  display: none;
}
.header-r__top {
  justify-content: flex-end;
  align-items: center;
}
.header-r__bottom {
  display: flex;
  justify-content: flex-end;
}

/* utility menu */
.menu-utility__top ul,
.menu-utility__bottom ul,
#nav-content #menu-utility-top-1 {
  list-style-type: none;
  display: flex;
}
.menu-utility__top ul {
  justify-content: flex-end;
}
.menu-utility__top ul li,
#nav-content #menu-utility-top-1 li {
  position: relative;
  padding-left: 13px;
  margin-right: 15px;
}
.menu-utility__top ul li::before,
.menu-utility__bottom ul li::before,
#nav-content #menu-utility-bottom-1 li::before,
#nav-content #menu-utility-top-1 li::before,
.link-orange-ribbon::before,
.link-clinic::before {
  display: block;
  content: '';
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 2.5px 0 2.5px 5px;
  position: absolute;
}
.menu-utility__top ul li::before,
#nav-content #menu-utility-top-1 li::before {
  border-color: transparent transparent transparent $primary-color;
  top: calc(50% - 3px);
  left: 0;
}
.menu-utility__top ul li a,
#nav-content #menu-utility-top-1 li a {
  text-decoration: none;
  color: #666;
}
.menu-utility__top ul li a {
  font-size: 14px;
  letter-spacing: 1px;
}
.menu-utility__top ul li a:hover {
  color: $secondary-color;
}
.menu-utility__bottom ul {
  flex-wrap: wrap;
  width: 342px;
}
.menu-utility__bottom ul li,
#nav-content #menu-utility-bottom-1 li {
  position: relative;
}
.menu-utility__bottom ul li {
  margin-right: 4px;
  margin-bottom: 4px;
}
#nav-content #menu-utility-bottom-1 li {
  margin-bottom: 5px;
}
.menu-utility__bottom ul li::before,
#nav-content #menu-utility-bottom-1 li::before,
.link-orange-ribbon::before {
  border-color: transparent transparent transparent #fff;
}
.link-clinic::before {
  border-color: transparent transparent transparent #333;
}
.menu-utility__bottom ul li::before,
#nav-content #menu-utility-bottom-1 li::before,
.link-orange-ribbon::before,
.link-clinic::before {
  top: calc(50% - 3px);
}
.menu-utility__bottom ul li::before {
  left: 8px;
}
.header-r__top .link-orange-ribbon::before,
.header-r__top .link-clinic::before {
  left: 10px;
}
#nav-content #menu-utility-bottom-1 li::before,
#nav-content .link-orange-ribbon::before,
#nav-content .link-clinic::before {
  left: 14px;
}
.menu-utility__bottom ul li:nth-child(2n) {
  margin-right: 0;
}
.menu-utility__bottom ul li:nth-child(3),
.menu-utility__bottom ul li:nth-child(4) {
  margin-bottom: 0;
}
.menu-utility__bottom ul li a,
#nav-content #menu-utility-bottom-1 li a {
  background-color: $primary-color;
  color: #fff;
  padding: 5px 4px;
  border-radius: 2px;
  display: block;
  text-align: center;
}
.menu-utility__bottom ul li a {
  width: 164px;
  font-size: 12px;
}
#nav-content #menu-utility-bottom-1 li a {
  width: 100%;
}
.menu-utility__bottom ul li a:hover {
  background-color: $secondary-color;
}

/* telephone number */
.telno,
.telno__forsp {
  width: 280px;
  height: 60px;
  border-radius: 30px;
  background-color: $base-color;
  color: $secondary-color;
  justify-content: center;
  align-items: center;
}
.telno__forsp {
  display: flex;
  margin: 10px auto;
}

/* links */
.link-orange-ribbon,
.link-clinic {
  text-align: center;
  position: relative;
}
.link-orange-ribbon {
  background-color: $highlight-color01;
  color: #fff;
}
.link-clinic {
  background-color: $highlight-color02;
  color: #333;
}
.header-r__top .link-orange-ribbon,
.header-r__top .link-clinic {
  width: 180px;
  margin: 10px 4px 10px 0;
  height: 24px;
  line-height: 24px;
  border-radius: 12px;
  font-size: 12px;
}
#nav-content .link-orange-ribbon,
#nav-content .link-clinic {
  width: 80%;
  margin: 10px auto;
  height: 34px;
  line-height: 34px;
  border-radius: 17px;
  font-size: 14px;
}

/* PC用global menu */
#st-menubox {
  border: 0;
}
#st-menubox ul.menu {
  display: flex;
  margin-top: 20px;
}
header .smanone ul.menu li {
  border: 0;
}
#st-menubox ul.menu li {
  width: 176px;
}
#st-menubox ul.menu > li {
  float: none;
  border: 0;
  border-right: 2px dotted $primary-color;
  height: 24px;
}
#st-menubox ul.menu > li:first-child {
  border-left: 2px dotted $primary-color;
}
#st-menubox ul.menu > li > a {
  line-height: 24px;
}
#st-menubox ul.menu > li > a:hover {
  color: $secondary-color;
}
#st-menubox ul.menu > li:hover ul.sub-menu {
  display: block;
}
#st-menubox ul.sub-menu {
  position: absolute;
  left: -1px;
}
#st-menubox ul.sub-menu > li > a {
  border: 0;
  border-bottom: 1px dotted $base-color;
  background-color: $primary-color;
  color: #fff;
}
#st-menubox ul.sub-menu > li:last-child > a {
  border: 0;
}
#st-menubox ul.sub-menu > li > a:hover {
  background-color: $secondary-color;
}

/* SP用 menu */
/* global */
#nav-content .menu-global-container .menu {
  width: 80%;
  margin: 10px auto;
}
#nav-content ul {
  list-style-type: none;
}
#nav-content .menu-global-container .menu li a {
  display: block;
  color: #333;
  text-align: left;
  border-bottom: 1px solid #eee;
  height: 38px;
  line-height: 38px;
  text-decoration: none;
}
#nav-content .menu-global-container .menu > li > a {
  position: relative;
}
#nav-content .menu-global-container .menu > li > a::after {
  content: '';
  border: solid $primary-color;
  border-width: 0 2px 2px 0;
  display: inline-block;
  padding: 4px;
  position: absolute;
  top: 12px;
  right: 0;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}
#nav-content .menu-global-container .menu > li.open > a::after {
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  right: 2px;
}
#nav-content .menu-global-container .sub-menu > li > a {
  margin-left: 20px;
}
#nav-content a {
  text-decoration: none !important;
}
/* submenu02 */
#nav-content #menu-utility-top-1,
#nav-content #menu-utility-bottom-1 {
  width: 80%;
  margin: 10px auto;
}
